.q-list
  &--bordered
    border: 1px solid $separator-color
  &--separator
    > .q-item-type + .q-item-type,
    > .q-virtual-scroll__content > .q-item-type + .q-item-type
      border-top: 1px solid $separator-color
  &--padding
    padding: 8px 0
.q-list--dense > .q-item, .q-item--dense
  min-height: 32px
  padding: 2px 16px
.q-list--dark.q-list--separator
  > .q-item-type + .q-item-type,
  > .q-virtual-scroll__content > .q-item-type + .q-item-type
    border-top-color: $separator-dark-color
.q-list--dark, .q-item--dark
  color: #fff
  border-color: $separator-dark-color
  .q-item__section--side:not(.q-item__section--avatar)
    color: rgba(255,255,255,.7)
  .q-item__label
    &--header
      color: rgba(255,255,255,.64)
    &--overline, &--caption
      color: rgba(255,255,255,.8)
.q-item
  position: relative
  &.q-router-link--active, &--active
    color: $primary
    color: var(--q-color-primary)
.q-knob
  font-size: 48px
  &--editable
    cursor: pointer
    outline: 0
    &:before
      content: ''
      position: absolute
      top: 0
      right: 0
      bottom: 0
      left: 0
      border-radius: 50%
      box-shadow: none
      transition: box-shadow .24s ease-in-out
    &:focus:before
      box-shadow: $shadow-4
.q-layout
  width: 100%
.q-layout-container
  position: relative
  width: 100%
  height: 100%
  .q-layout
    min-height: 100%
  > div
    transform: translate3d(0, 0, 0)
    > div
      min-height: 0
      max-height: 100%
.q-layout__shadow
  width: 100%
  &:after
    content: ''
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    box-shadow: $layout-shadow
.q-layout__section--marginal
  background-color: $primary
  background-color: var(--q-color-primary)
  color: #fff
.q-header
  &--hidden
    transform: translateY(-110%)
  &--bordered
    border-bottom: $layout-border
  .q-layout__shadow
    bottom: -10px
    &:after
      bottom: 10px
.q-footer
  &--hidden
    transform: translateY(110%)
  &--bordered
    border-top: $layout-border
  .q-layout__shadow
    top: -10px
    &:after
      top: 10px
.q-header, .q-footer
  z-index: $z-marginals
.q-drawer
  position: absolute
  top: 0
  bottom: 0
  background: #fff
  z-index: $z-side
  &--on-top
    z-index: $z-fixed-drawer
  &--left
    left: 0
    transform: translateX(-100%)
    &.q-drawer--bordered
      border-right: $layout-border
    .q-layout__shadow
      left: 10px
      right: -10px
      &:after
        right: 10px
  &--right
    right: 0
    transform: translateX(100%)
    &.q-drawer--bordered
      border-left: $layout-border
    .q-layout__shadow
      left: -10px
      &:after
        left: 10px
  &-container:not(&--mini-animate) &--mini
    padding: 0 !important
    .q-item, .q-item__section
      text-align: center
      justify-content: center
      padding-left: 0
      padding-right: 0
      min-width: 0
    .q-item__label, .q-item__section--main, .q-item__section--side ~ .q-item__section--side
      display: none
  &--mini
    .q-mini-drawer-hide, .q-expansion-item__content
      display: none
  &--mini-animate &__content
    overflow-x: hidden
    white-space: nowrap
  &--standard
    .q-mini-drawer-only
      display: none
  &--mobile
    .q-mini-drawer-only, .q-mini-drawer-hide
      display: none
  &__backdrop
    z-index: ($z-fixed-drawer - 1) !important
    will-change: background-color
  &__opener
    z-index: ($z-marginals + 1)
    height: 100%
    width: 15px
    user-select: none
.q-layout, .q-header, .q-footer, .q-page
  position: relative
.q-page-sticky--shrink
  pointer-events: none
  > div
    display: inline-block
    pointer-events: auto
body.q-ios-padding
  .q-layout--standard .q-header > .q-toolbar:nth-child(1),
  .q-layout--standard .q-header > .q-tabs:nth-child(1) .q-tabs-head,
  .q-layout--standard .q-drawer--top-padding .q-drawer__content
    padding-top: $ios-statusbar-height
    min-height: ($toolbar-min-height + $ios-statusbar-height)
    padding-top: env(safe-area-inset-top)
    min-height: calc(env(safe-area-inset-top) + #{$toolbar-min-height})
  .q-layout--standard .q-footer > .q-toolbar:last-child,
  .q-layout--standard .q-footer > .q-tabs:last-child .q-tabs-head,
  .q-layout--standard .q-drawer--top-padding .q-drawer__content
    padding-bottom: env(safe-area-inset-bottom)
    min-height: calc(env(safe-area-inset-bottom) + #{$toolbar-min-height})
.q-body--layout-animate
  .q-drawer__backdrop
    transition: background-color .12s !important
  .q-drawer
    transition: transform .12s, width .12s, top .12s, bottom .12s !important
  .q-layout__section--marginal
    transition: transform .12s, left .12s, right .12s !important
  .q-page-container
    transition: padding-top .12s, padding-right .12s, padding-bottom .12s, padding-left .12s !important
  .q-page-sticky
    transition: transform .12s, left .12s, right .12s, top .12s, bottom .12s !important
body:not(.q-body--layout-animate)
  .q-layout--prevent-focus
    visibility: hidden
.q-body--drawer-toggle
  overflow-x: hidden !important
.q-layout-padding
  @media (max-width: $breakpoint-xs-max)
    padding: 8px
  @media (min-width: $breakpoint-sm-min) and (max-width: $breakpoint-md-max)
    padding: 16px
  @media (min-width: $breakpoint-lg-min)
    padding: 24px
body.body--dark
  .q-header, .q-footer, .q-drawer
    border-color: $separator-dark-color
body.platform-ios
  .q-layout--containerized
    
    
    
    position: unset !important
.q-linear-progress
  position: relative
  width: 100%
  overflow: hidden
  font-size: 4px
  height: 1em
  color: $primary
  color: var(--q-color-primary)
  &__model, &__track
    transform-origin: 0 0
    &--with-transition
      transition: transform .3s
  &--reverse
    .q-linear-progress
      &__model, &__track
        transform-origin: 0 100%
  &__model
    &--determinate
      background: currentColor
    &--indeterminate, &--query
      transition: none
      &:before, &:after
        background: currentColor
        content: ''
        position: absolute
        top: 0
        right: 0
        bottom: 0
        left: 0
        transform-origin: 0 0
      &:before
        animation: q-linear-progress--indeterminate 2.1s cubic-bezier(.65,.815,.735,.395) infinite
      &:after
        transform: translate3d(-101%, 0, 0) scale3d(1, 1, 1)
        animation: q-linear-progress--indeterminate-short 2.1s cubic-bezier(.165,.84,.44,1) infinite
        animation-delay: 1.15s
  &__track
    opacity: .4
    &--light
      background: rgba(0,0,0,.26)
    &--dark
      background: rgba(255,255,255,.6)
  &__stripe
    transition: width .3s
    background-image: linear-gradient(45deg, rgba(255,255,255,.15) 25%, rgba(255,255,255,0) 25%, rgba(255,255,255,0) 50%, rgba(255,255,255,.15) 50%, rgba(255,255,255,.15) 75%, rgba(255,255,255,0) 75%, rgba(255,255,255,0)) !important
    background-size: 40px 40px !important
@keyframes q-linear-progress--indeterminate
  0%
    transform: translate3d(-35%, 0, 0) scale3d(.35, 1, 1)
  60%
    transform: translate3d(100%, 0, 0) scale3d(.9, 1, 1)
  100%
    transform: translate3d(100%, 0, 0) scale3d(.9, 1, 1)
@keyframes q-linear-progress--indeterminate-short
  0%
    transform: translate3d(-101%, 0, 0) scale3d(1, 1, 1)
  60%
    transform: translate3d(107%, 0, 0) scale3d(.01, 1, 1)
  100%
    transform: translate3d(107%, 0, 0) scale3d(.01, 1, 1)
.q-menu
  position: fixed !important
  display: inline-block
  max-width: $menu-max-width
  box-shadow: $menu-box-shadow
  background: $menu-background
  border-radius: $generic-border-radius
  overflow-y: auto
  overflow-x: hidden
  outline: 0
  max-height: 65vh
  z-index: $z-menu
  &--square
    border-radius: 0
.q-option-group
  &--inline
    > div
      display: inline-block
.q-pagination
  input
    text-align: center
    -moz-appearance: textfield
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button
    -webkit-appearance: none
    margin: 0
.q-parallax
  position: relative
  width: 100%
  overflow: hidden
  border-radius: inherit
.q-parallax__media
  > img, > video
    position: absolute
    left: 50% #{"/* rtl:ignore */"}
    bottom: 0
    min-width: 100%
    min-height: 100%
    will-change: transform
    display: none
.q-popup-edit
  padding: 8px 16px
  &__buttons
    margin-top: 8px
    .q-btn + .q-btn
      margin-left: 8px
.q-pull-to-refresh
  position: relative
  &__puller
    border-radius: 50%
    width: 40px
    height: 40px
    color: $primary
    color: var(--q-color-primary)
    background: #fff
    box-shadow: 0 0 4px 0 rgba(0,0,0,.3)
    &--animating
      transition: transform .3s, opacity .3s
$radio-transition: .22s cubic-bezier(0,0,.2,1) 0ms
.q-radio
  vertical-align: middle
  &__bg
    top: 25%
    left: 25%
    width: 50%
    height: 50%
    path
      fill: currentColor
  &__native
    width: 1px
    height: 1px
  &__check
    transform-origin: 50% 50%
    transform: scale3d(0, 0, 1)
    transition: transform $radio-transition
  &__inner
    font-size: 40px
    width: 1em
    min-width: 1em
    height: 1em
    outline: 0
    border-radius: 50%
    color: rgba(0,0,0,.54)
    &--truthy
      color: $primary
      color: var(--q-color-primary)
      .q-radio__check
        transform: scale3d(1, 1, 1)
  &.disabled
    opacity: .75 !important
  &--dark
    .q-radio__inner
      color: rgba(255,255,255,.7)
      &:before
        opacity: .32 !important
      &--truthy
        color: $primary
        color: var(--q-color-primary)
  &--dense
    .q-radio__inner
      width: .5em
      min-width: .5em
      height: .5em
    .q-radio__bg
      left: 0
      top: 0
      width: 100%
      height: 100%
    .q-radio__label
      padding-left: .5em
    &.reverse .q-radio__label
      padding-left: 0
      padding-right: .5em
body.desktop
  .q-radio:not(.disabled)
    .q-radio__inner:before
      content: ''
      position: absolute
      top: 0
      right: 0
      bottom: 0
      left: 0
      border-radius: 50%
      background: currentColor
      opacity: .12
      transform: scale3d(0, 0, 1)
      transition: transform $radio-transition
    &:focus,
    &:hover
      .q-radio__inner:before
        transform: scale3d(1, 1, 1)
  .q-radio--dense:not(.disabled)
    &:focus,
    &:hover
      .q-radio__inner:before
        transform: scale3d(1.5, 1.5, 1)
.q-rating
  color: $rating-grade-color
  vertical-align: middle
  &__icon
    color: currentColor
    text-shadow: $rating-shadow
    position: relative
    opacity: .4
    transition: transform .2s ease-in, opacity .2s ease-in
    &--hovered
      transform: scale(1.3)
    &--active
      opacity: 1
    &--exselected
      opacity: .7
    & + &
      margin-left: 2px
  &--no-dimming .q-rating__icon
    opacity: 1
  &--editable .q-icon
    cursor: pointer
  &--non-editable span, .q-icon
    outline: 0
.q-responsive
  position: relative
  max-width: 100%
  max-height: 100%
  &__filler
    width: inherit
    max-width: inherit
    height: inherit
    max-height: inherit
  &__content
    border-radius: inherit
    > *
      width: 100% !important
      height: 100% !important
      max-height: 100% !important
      max-width: 100% !important
.q-scrollarea
  position: relative
  &__bar,
  &__thumb
    opacity: .2
    transition: opacity .3s
    will-change: opacity
    cursor: grab
    &--v
      right: 0
      width: 10px
    &--h
      bottom: 0
      height: 10px
    &--invisible
      opacity: 0 !important
      pointer-events: none
  &__thumb
    background: #000
    &:hover
      opacity: .3
    &:active
      opacity: .5
  &--dark .q-scrollarea__thumb
    background: #fff
.q-select
  &--without-input
    .q-field__control
      cursor: pointer
  &--with-input
    .q-field__control
      cursor: text
  .q-field__input
    min-width: 50px !important
    &--padding
      padding-left: 4px
  &__autocomplete-input
    width: 0
    height: 0
    padding: 0
    border: 0
    opacity: 0
  &__dropdown-icon
    cursor: pointer
    transition: transform .28s
  &.q-field--readonly
    .q-field__control, .q-select__dropdown-icon
      cursor: default
  &__dialog
    width: 90vw !important
    max-width: 90vw !important
    max-height: calc(100vh - 70px) !important
    background: #fff
    display: flex
    flex-direction: column
    > .scroll
      position: relative
      background: inherit
body.mobile:not(.native-mobile) .q-select__dialog
  max-height: calc(100vh - 108px) !important
body.platform-android
  &.native-mobile .q-dialog__inner--top .q-select__dialog
    max-height: calc(100vh - 24px) !important
  &:not(.native-mobile) .q-dialog__inner--top .q-select__dialog
    max-height: calc(100vh - 80px) !important
body.platform-ios
  &.native-mobile .q-dialog__inner--top
    > div
      border-radius: $generic-border-radius
    .q-select__dialog--focused
      max-height: 47vh !important
  &:not(.native-mobile) .q-dialog__inner--top .q-select__dialog--focused
    max-height: 50vh !important
.q-separator
  border: 0
  background: $separator-color
  margin: 0
  transition: background .3s, opacity .3s
  flex-shrink: 0
  &--dark
    background: $separator-dark-color
  &--horizontal
    display: block
    height: 1px
    &-inset
      margin-left: 16px
      margin-right: 16px
    &-item-inset
      margin-left: 72px
      margin-right: 0
    &-item-thumbnail-inset
      margin-left: 116px
      margin-right: 0
  &--vertical
    width: 1px
    height: auto
    align-self: stretch
    &-inset
      margin-top: 8px
      margin-bottom: 8px
.q-skeleton
  background: $separator-color
  border-radius: $generic-border-radius
  &--anim
    cursor: wait
  
  box-sizing: border-box
  &:before
    content: '\00a0'
  &--type
    &-text
      transform: scale(1, .5)
    &-circle,
    &-QAvatar
      height: 48px
      width: 48px
      border-radius: 50%
    &-QBtn
      width: 90px
      height: 36px
    &-QBadge
      width: 70px
      height: 16px
    &-QChip
      width: 90px
      height: 28px
      border-radius: 16px
    &-QToolbar
      height: 50px
    &-QCheckbox,
    &-QRadio
      width: 40px
      height: 40px
      border-radius: 50%
    &-QToggle
      width: 56px
      height: 40px
      border-radius: 7px
    &-QSlider,
    &-QRange
      height: 40px
    &-QInput
      height: 56px
  &--bordered
    border: 1px solid rgba(0,0,0,.05)
  &--square
    border-radius: 0
  &--anim-fade
    animation: q-skeleton--fade 1.5s linear .5s infinite
  &--anim-pulse
    animation: q-skeleton--pulse 1.5s ease-in-out .5s infinite
  &--anim-pulse-x
    animation: q-skeleton--pulse-x 1.5s ease-in-out .5s infinite
  &--anim-pulse-y
    animation: q-skeleton--pulse-y 1.5s ease-in-out .5s infinite
  &--anim-wave,
  &--anim-blink,
  &--anim-pop
    position: relative
    overflow: hidden
    z-index: 1
    &:after
      content: ''
      position: absolute
      top: 0
      right: 0
      bottom: 0
      left: 0
      z-index: 0
  &--anim-blink:after
    background: rgba(255,255,255,.7)
    animation: q-skeleton--fade 1.5s linear .5s infinite
  &--anim-wave:after
    background: linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,.5), rgba(255,255,255,0))
    animation: q-skeleton--wave 1.5s linear .5s infinite
  &--dark
    background: rgba(255, 255, 255, 0.05)
    &.q-skeleton--bordered
      border: 1px solid rgba(255,255,255,.25)
    &.q-skeleton--anim-wave:after
      background: linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,.1), rgba(255,255,255,0))
    &.q-skeleton--anim-blink:after
      background: rgba(255,255,255,.2)
@keyframes q-skeleton--fade
  0%
    opacity: 1
  50%
    opacity: .4
  100%
    opacity: 1
@keyframes q-skeleton--pulse
  0%
    transform: scale(1)
  50%
    transform: scale(.85)
  100%
    transform: scale(1)
@keyframes q-skeleton--pulse-x
  0%
    transform: scaleX(1)
  50%
    transform: scaleX(.75)
  100%
    transform: scaleX(1)
@keyframes q-skeleton--pulse-y
  0%
    transform: scaleY(1)
  50%
    transform: scaleY(.75)
  100%
    transform: scaleY(1)
@keyframes q-skeleton--wave
  0%
    transform: translateX(-100%)
  100%
    transform: translateX(100%)
.q-slide-item
  position: relative
  background: white
  &__left, &__right, &__top, &__bottom
    visibility: hidden
    font-size: 14px
    color: #fff
    .q-icon
      font-size: 1.714em
  &__left
    background: $green
    padding: 8px 16px
    > div
      transform-origin: left center
  &__right
    background: $orange
    padding: 8px 16px
    > div
      transform-origin: right center
  &__top
    background: $blue
    padding: 16px 8px
    > div
      transform-origin: top center
  &__bottom
    background: $purple
    padding: 16px 8px
    > div
      transform-origin: bottom center
  &__content
    background: inherit
    transition: transform .2s ease-in
    user-select: none
    cursor: pointer
.q-slider
  position: relative
  color: $primary
  color: var(--q-color-primary)
  outline: 0
  &--h
    width: 100%
    height: 40px
  &--v
    width: 40px
    height: 200px
  &__track-container
    background: rgba(0,0,0,.26)
    &--h
      top: 50%
      margin-top: -1px
      width: 100%
      height: 2px
    &--v
      left: 50%
      margin-left: -1px
      height: 100%
      width: 2px
  &__track
    background: currentColor
    &--h
      will-change: width, left
      top: 0
      bottom: 0
    &--v
      will-change: height, top
      left: 0
      right: 0
  &__track-markers
    color: #000
    &--h
      background-image: repeating-linear-gradient(to right, currentColor, currentColor 2px, rgba(255,255,255,0) 0, rgba(255,255,255,0))
    &--v
      background-image: repeating-linear-gradient(to top, currentColor, currentColor 2px, rgba(255,255,255,0) 0, rgba(255,255,255,0))
    &:after
      content: ''
      position: absolute
      right: 0
      top: 0
      bottom: 0
      height: 2px
      width: 2px
      background: currentColor
  &__thumb-container
    width: 20px
    height: 20px
    outline: 0
    &--h
      top: 50%
      margin-top: -10px
      transform: translateX(-10px) #{"/* rtl:ignore */"}
      will-change: left
    &--v
      left: 50%
      margin-left: -10px
      transform: translateY(-10px)
      will-change: top
  &__thumb
    top: 0
    left: 0
    transform: scale(1)
    transition: transform .18s ease-out, fill .18s ease-out, stroke .18s ease-out
    stroke-width: 3.5
    stroke: currentColor
    path
      stroke: currentColor
      fill: currentColor
  &__focus-ring
    width: 20px
    height: 20px
    transition: transform 266.67ms ease-out, opacity 266.67ms ease-out, background-color 266.67ms ease-out
    border-radius: 50%
    opacity: 0
    transition-delay: .14s
  &__arrow
    position: absolute
    width: 0
    height: 0
    transform-origin: 50% 50%
    transition: transform 100ms ease-out
    &--h
      top: 19px
      left: 4px
      border-top: 6px solid currentColor
      border-left: 6px solid transparent
      border-right: 6px solid transparent
      transform: scale(0) translateY(0)
    &--v
      top: 4px
      left: 15px
      border-top: 6px solid transparent
      border-bottom: 6px solid transparent
      border-right: 6px solid currentColor
      transform: scale(0) translateX(-20px)
  &__pin
    transition: transform 100ms ease-out
    z-index: 1
    white-space: nowrap
    &--h
      bottom: 0
      right: 0
      transform: scale(0) translateY(0)
      will-change: left
    &--v
      top: 50%
      left: 20px
      height: 0
      transform: scale(0) translateX(-20px)
      will-change: top
      transform-origin: left center
    &-text-container
      min-height: 25px
      padding: 2px 8px
      border-radius: $generic-border-radius
      background: currentColor
      position: relative
      text-align: center
      &--h
        right: -50%
      &--v
        transform: translateY(-50%)
    &-text
      color: #fff
      font-size: 12px
  &--editable
    cursor: grab
  &--no-value
    .q-slider__thumb, .q-slider__track
      visibility: hidden
  &--focus
    .q-slider__thumb
      transform: scale(1)
  &--focus, body.desktop &.q-slider--editable:hover
    .q-slider__focus-ring
      background: currentColor
      transform: scale3d(1.55, 1.55, 1)
      opacity: .25
    .q-slider__thumb, .q-slider__track
      visibility: visible
  &--inactive
    .q-slider__thumb-container
      &--h
        transition: left .28s, right .28s
      &--v
        transition: top .28s, bottom .28s
    .q-slider__track
      &--h
        transition: width .28s, left .28s, right .28s
      &--v
        transition: height .28s, top .28s, bottom .28s
  &--active
    cursor: grabbing
    .q-slider__thumb
      transform: scale(1.5)
    .q-slider__focus-ring, &.q-slider--label .q-slider__thumb
      transform: scale(0) !important
