<template>  
  <div class="planetar-tabs">
    <QTabs
      v-model="activeTabIndex"
      dense
      class="bg-stone c-grey-3 _top"
      active-color="primary"
      indicator-color="stone"
      align="left"
      narrow-indicator
    >
      <QTab
        v-for="(label, index) in tabLabels"
        :key="label + index"
        :name="index"
        :label="label"
        no-caps
      />
    </QTabs>
    <QTabPanels
      v-model="activeTabIndex"
      animated
      class="_bottom"
      v-bind="propsToPass"
      v-on="$listeners"
    >
      <QTabPanel
        v-for="(label, index) in tabLabels"
        :key="label + index"
        :name="index"
        class="no-padding full-height"
      >
        <!--
          @slot Tab Panels are automatically added by adding them with their "index" number of each label passed in the 'tabLabels' prop. Eg. `<div v-slot:0>first panel</div><div v-slot:1>second panel</div>`
        -->
        <slot :name="index" />
      </QTabPanel>
    </QTabPanels>
  </div>
</template>

<script>
import { QTabs, QTab, QTabPanels, QTabPanel } from 'quasar';
/**
 * This is the main input field.
 */

export default {
  name: 'PlanetarTabs',
  components: {
    QTabs: QTabs,
    QTab: QTab,
    QTabPanels: QTabPanels,
    QTabPanel: QTabPanel
  },
  props: {
    /**
     * The labels of the tabs.
     * @category content
     * @type {string[]}
     * @example ['First Tab', 'Second Tab']
     */
    tabLabels: {
      type: Array,
      default: function _default() {
        return [];
      },
      required: true
    },

    /**
     * The tab which should be opened initially. Can be used with `.sync`
     * @category content
     * @type {string}
     */
    activeTab: {
      type: String
    }
  },
  data: function data() {
    var _this = this;

    var i = this.tabLabels.findIndex(function (label) {
      return label === _this.activeTab;
    });
    var activeTabIndexInner = Math.max(0, i);
    return {
      activeTabIndexInner: activeTabIndexInner
    };
  },
  computed: {
    activeTabIndex: {
      get: function get() {
        return this.activeTabIndexInner;
      },
      set: function set(newIndex) {
        this.activeTabIndexInner = newIndex;
        var activeTab = this.tabLabels[newIndex];
        this.$emit('update:active-tab', activeTab);
      }
    },
    propsToPass: function propsToPass() {
      var $attrs = this.$attrs;
      return Object.assign({}, $attrs);
    }
  },
  methods: {}
};
</script>

